body, html, #root {
    margin: 0;
    padding: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  
  .App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .content{
    text-align: left;
  }
  main {
    flex: 1; /* This makes the main content take up the available space */
    padding: 20px;
  }