
nav {
  background-color: #333;
}

nav ul {
  list-style-type: none; 
  margin: 0; 
  padding: 0; 
  overflow: hidden; 
}

nav ul li {
  float: left; 
}

nav ul li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px; /* Padding around menu items */
  text-decoration: none;
}

nav ul li a:hover {
  background-color: #111; /* Change background color on hover */
}